.header{top:0;z-index:9999;position:fixed;font-family:Roboto, Arial, Helvetica, "Droid Sans", sans-serif;color:#e7e6e3;background-color:#000707;font-size:1.1rem;font-weight:600;transition:top ease 0.3s}.header a:hover{color:tan;-webkit-filter:brightness(1.2);filter:brightness(1.2)}img{width:100%;object-fit:contain}a{text-decoration:none;color:currentColor;cursor:pointer}li{list-style:none}ul{margin:0;padding:0}.account{font-size:1.3rem}.avatar{width:45px}.avatar img{border-radius:50%}.avatar:hover{-webkit-filter:brightness(1.2);filter:brightness(1.2);cursor:pointer}.logo{cursor:pointer;margin-right:2rem}.logo:hover .logo_dropdown_icon path{fill:#fff}.riot_products{z-index:9998;color:#333333;background-color:#fff;width:calc(100vw - 1.1rem);overflow-y:scroll;left:-1rem;font-family:"Raleway", sans-serif;font-weight:900;font-size:1.8rem;transition:all 1 ease-in}@media screen and (max-width: 765px){.riot_products{width:calc(80vw - 0.8rem);height:calc(100vh - 69px)}}.game_poster{padding:0.25rem;height:100%;height:inherit}.game_poster_big{padding:0.25rem;height:inherit}@media screen and (max-width: 576px){.game_poster{width:50%;padding:0.25rem;height:100%;height:inherit}.game_poster_big{width:100%;padding:0.25rem;height:inherit}}.hbgMenu{cursor:pointer}.nav_mobile{background-color:rgba(0,7,7,0.7);position:fixed;display:flex;flex-direction:column;align-items:center;top:69px;left:-15px;height:100vh;width:100vw;transition:all linear 0.2s}.nav_mobile li{text-align:center}.nav_mobile li:hover{background-color:rgba(1,27,27,0.6)}.nav_mobile li:hover a{-webkit-filter:brightness(1.2);filter:brightness(1.2)}.nav_mobile.hidden{opacity:0.3;-webkit-transform:translateX(150%);transform:translateX(150%);left:-15px;height:100vh;width:100vw}.nav_mobile.show{opacity:1;-webkit-transform:translateX(0);transform:translateX(0);left:-15px;height:100vh;width:100vw}.search_bar{background-color:#2b2a29;border-radius:30px 30px 30px 30px;max-width:200px;height:35px;padding:0.25rem 1.5rem;float:right}.search_bar input{color:#fff;font-size:0.9rem;max-width:100px}.search_bar input:focus-visible{outline:none}.download:hover .download_dropdown,.game-info:hover .game-info_dropdown,.support:hover .support_dropdown{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.game-info_dropdown,.support_dropdown,.download_dropdown{opacity:0.8;background-color:#000707;height:0;overflow-y:hidden;font-size:1rem;line-height:2rem;transition:all ease-in 0.3s;width:-webkit-max-content;width:max-content;top:69x;left:0}.game-info_dropdown li,.support_dropdown li,.download_dropdown li{padding:1rem}.game-info_dropdown li:hover,.support_dropdown li:hover,.download_dropdown li:hover{background-image:url(/Briot/static/media/riotbg.74927980.jpg);background-size:cover}.game-info_dropdown li:hover a,.support_dropdown li:hover a,.download_dropdown li:hover a{color:tan;-webkit-filter:brightness(1.2);filter:brightness(1.2)}.game-info_dropdown_mobile,.support_dropdown_mobile,.download_dropdown_mobile{height:0;overflow:hidden}

